@import "./variables.scss";

.social {
  @media (max-width: $vp-1023) {
    margin-top: 5px;
  }

  @media (max-width: $vp-767) {
    margin-top: 0;
  }
}

.social ul {
  display: flex;
  margin: 0;
  padding: 0;

  list-style: none;

  @media (max-width: $vp-767) {
    justify-content: center;
    margin-right: 12px;
    padding-bottom: 25px;
  }
}

.social li {
  width: 40px;
  height: 40px;
  margin-left: 12px;

  &:last-child {
    margin-right: 0;
  }

  &:last-child > a {
    @media (max-width: $vp-1023) {
      justify-content: flex-end;
    }
  }
}

.social a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
